import tuiIcon from "@/components/icon/icon"
import Coupon from "@/components/qy/Coupon"
import tuiTag from "@/components/tag/tag"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
import Cache from "@/common/cache.js"
import xianshi from "@/components/qy/xianshi"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiLoadmore,
		tuiNomore,
		Coupon,
		xianshi
	},
	data() {
		return {
			resou: '',
			coulist: [1, 2, 3, 4],
			coupon: true,
			is_vip: 0,
			getimg: this.$getimg,
			current: 0,
			tabbar: [{
				icon: "home",
				text: "首页",
				size: 21
			}, {
				icon: "category",
				text: "分类",
				size: 24
			}, {
				icon: "cart",
				text: "购物车",
				size: 22
			}, {
				icon: "people",
				text: "我的",
				size: 24
			}],
			hotSearch: [],
			banner: [],
			category: [],
			newProduct: [],
			productList: [],

			pageIndex: 1,
			loadding: false,
			pullUpOn: true
		}
	},
	onLoad() {
		this._load()
		console.log(this.$getimg)
		// this.laoddata()
		// let time = Date.parse(new Date()) / 1000 //当前时间

		// const cache = uni.getStorageSync('home'); //抓取缓存
		// if (!cache || this._CheckCacheTime(cache.cache_time, 0.2)) { //判断缓存是否存在
		// 	this._load()
		// } else {        cue shizdyoudiannan let cache = uni.getstorege a happy journey
		// 	this.category = cache.data[0].data
		// 	this.newProduct = cache.data[1].data
		// 	this.productList = cache.data[2].data
		// 	this.banner = cache.data[3].data

		// }
	},
	methods: {
		close_add() {
			this.coupon = false
		},
		tabbarSwitch: function(e) {
			let index = e.currentTarget.dataset.index;
			this.current = index;
			if (index != 0) {
				if (index == 1) {
					this.classify();
				} else if (index == 2) {
					uni.switchTab({
						url: '/pages/cart/cart'
					})
				} else if (index == 3) {
					uni.switchTab({
						url: '/pages/user/user'
					})
				}
			}
		},
		_load() {
			let time = Date.parse(new Date()) / 1000 //当前时间
			// this.$api.http.get('product/get_recent',{type:''}).then(res=>{
			// 	this.newProduct = res.data
			// })
			this.$api.http.get('product/get_recent', {
				type: 'hot'
			}).then(res => { //热门
				this.productList = res.data
				console.log(this.productList)
			})
			this.$api.http.get('product/get_recent', {
				type: 'new'
			}).then(res => { //新品
				this.newProduct = res.data
			})
			this.$api.http.get('nav/get_nav').then(res => {
				this.category = res.data
			})
			this.$api.http.get('banner/get_banner?id=1').then(res => { //首页banner
				this.banner = res.data.items
				console.log(this.banner)
			})
			this.$api.http.get('search/record').then(res => { //首页banner
				this.resou = res.data
				this.hotSearch = res.data.slice(0, 3)
				uni.setStorageSync('hotSearch', this.resou)
				console.log('hot:', this.hotSearch)
			})
			//let a = this.$api.http.get('get_category')
			//let b = this.$api.http.get('product/get_recent')
			//let c = this.$api.http.get('get_productList')
			//let d = this.$api.http.get('get_banner')
			// Promise.all([a, b, c, d]).then(res => {
			// 	this.category = res[0].data
			// 	this.newProduct = res[1].data
			// 	this.productList = res[2].data
			// 	this.banner = res[3].data
			// 	Cache._set_home_cache(res)

			// })

		},

		// async laoddata(){
		// 	this.productList = await this.$api.json('productList')
		// 	console.log(this.productList)
		// 	console.log(1)
		// },
		// 
		_CheckCacheTime(times, xs = 5) {
			const time = Date.parse(new Date()) / 1000 //当前时间
			const end_time = times + 60 * xs
			return time > end_time ? true : false
		},
		detail: function(id) {
			uni.navigateTo({
				url: '/pages/extend-view/productDetail/productDetail?id=' + id
			})
		},
		classify: function() {
			uni.switchTab({
				url: '/pages/category/category'
			})

		},
		more: function(e) {
			let key = e.currentTarget.dataset.key || "";
			uni.navigateTo({
				url: '/pages/extend-view/productList/productList?searchKey=' + key
			})

		},
		search: function() {
			uni.navigateTo({
				url: '/pages/extend-view/news-search/news-search'
			})
		},
		jump_article(id,type) {
			let url='/pages/article/article?id='+id
			if(type=='goods'){
				url='/pages/extend-view/productDetail/productDetail?id='+id
			}
			uni.navigateTo({
				url: url
			});
		},
		tosearch(item) {
			console.log(this.resou)
			console.log(item)
			uni.navigateTo({
				url:'../extend-view/productList/productList?key='+item
			})
		}
	},
	onPullDownRefresh() {
		this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	},
	onShareAppMessage(res) {
		return {
			title: '如花',
		}
	}
}
